import React, { useContext } from "react"
import { Link } from "gatsby"
import styled from "styled-components"
import { MenuState } from "../components/layout"
import HoverStroke from "../images/hoverstroke.svg"

const MenuStyle = styled.div`
  flex-direction: column;
  width: 100%;
  margin-top: 0.5rem;
  font-family: "Stylish", sans-serif;
  @media (max-width: 562px) {
    display: ${props => (props.toggle ? "flex" : "none")};
    font-size: 1.1rem;
    & a {
      padding: 0.7rem 0;
    }
  }
  @media (min-width: 762px) {
    display: flex;
    font-size: 1rem;
    & a {
      padding: 0.3rem 0;
    }
  }
  @media (min-width: 1000px) {
    display: flex;
    font-size: 1rem;
    & a {
      padding: 0.3rem 0;
    }
  }
  & a {
    text-decoration: none;
    width: 100%;
    margin-left: 1rem 0 1rem 0.6rem;
    color: #fff;
    font-weight: bold;
    text-align: center;
    border-bottom: 1px solid #333;

    transition: all 0.3s ease;
    &:hover {
      cursor: pointer;
      background: url(${HoverStroke});
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100%;

      &:active {
        transform: scale(1.05);
      }
    }
  }
`

const Menu = ({ toggle }) => {
  const [
    menu,
    setMenu,
    articleActive,
    setArticle,
    projectActive,
    setProject,
    contactActive,
    setContact,
    confirm,
    setConfirm,
    blurbActive,
    setBlurb,
  ] = useContext(MenuState)

  return (
    <MenuStyle toggle={toggle}>
      <Link
        to="/"
        onClick={() => {
          setArticle(false)
          setProject(false)
          setMenu(false)
          setBlurb(true)
        }}
      >
        HOME
      </Link>
      <Link
        to="/"
        onClick={() => {
          setArticle(true)
          setProject(false)
          setBlurb(false)
        }}
      >
        ARTICLES
      </Link>
      <Link
        to="/"
        onClick={() => {
          setProject(true)
          setArticle(false)
          setBlurb(false)
        }}
      >
        PROJECTS
      </Link>
      <Link
        to="/about"
        onClick={() => {
          setBlurb(false)
          setMenu(false)
        }}
      >
        ABOUT
      </Link>
    </MenuStyle>
  )
}
export default Menu
