import React, { useEffect } from "react"
import { useSpring, animated } from "react-spring"
import styled from "styled-components"
import SuccessBG from "../images/popup-bg1.svg"
import ErrorBG from "../images/popup-bg2.svg"

const Popup = styled(animated.div)`
  ...;
  position: absolute;
  left: 49%;
  top: 1%;

  height: auto;
  background-color: red;
  transform: translateX(-50%);
  text-align: center;
  padding: 1rem 0;
  transition: all 0.5s ease-in-out;
  & p {
    margin-bottom: 0;
    color: #fff;
    font-family: "Stylish", sans-serif;
  }
  display: ${props =>
    props.type === "success" || props.type === "error" ? "block" : "none"};
  background: ${props =>
    props.type === "success"
      ? `url(${SuccessBG})`
      : props.type === "error"
      ? `url(${ErrorBG})`
      : "none"};
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;

  @media (max-width: 562px) {
    width: 90%;
    height: 20%;
    & p {
      margin-top: 10%;
    }
  }
  @media (min-width: 762px) {
    width: 40%;
    height: 10%;
    & p {
      margin-top: 5%;
    }
  }
  @media (min-width: 992px) {
    width: 20%;
    height: 10%;
    & p {
      margin-top: 0;
    }
  }
`

const Confirm = ({ props: { confirm, setConfirm } }) => {
  const PopupFade = useSpring({
    opacity: confirm.state === "success" || confirm.state === "error" ? 1 : 0,
    duration: 500,
  })
  useEffect(() => {
    const timeout = setTimeout(() => {
      setConfirm({ state: "", msg: "" })
    }, 10000)
    return () => clearTimeout(timeout)
  }, [confirm.state])
  return (
    <Popup style={PopupFade} type={confirm.state}>
      <p>{confirm.msg}</p>
    </Popup>
  )
}

export default Confirm
