import PropTypes from "prop-types"
import React, { useContext, useState } from "react"
import Menu from "./menu"
import styled from "styled-components"
import { MenuState } from "./layout"
import MenuIcon from "../images/icons/menu-01.svg"
import LinkedIcon from "../images/icons/linkedin.svg"
import EmailIcon from "../images/icons/envelope.svg"
import GithubIcon from "../images/icons/github.svg"
import BlackBG from "../images/calligraphy_bg1.png"
import Sword from "../images/sword_cropped.png"
import JpLogo from "../images/johnphunglogo.png"

const NavbarStyle = styled.header`
  background: url(${BlackBG});
  background-repeat: no-repeat;
  @media (max-width: 562px) {
    background-size: ${props => (props.toggle ? "150%" : "100%")};
    background-position: ${props => (props.toggle ? "right" : "top")};
  }
  @media (min-width: 762px) {
    background-size: 180%;
    background-position: 90%;
  }

  @media (min-width: 1001px) {
    background-size: 180%;
    background-position: right;
  }
  background-size: cover;
  background-position: top right;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
  transition: all 0.5s;
  @media (max-width: 576px) {
    width: ${props => (props.toggle ? "60%" : "15%")};
    align-self: center;
  }

  @media (min-width: 768px) {
    width: 10.5rem;
    align-self: center;
  }

  @media (min-width: 992px) {
    width: 10.5rem;
    align-self: center;
  }
`

const SwordImage = styled.img`
  pointer-events: none;
  position: relative;
  left: 0.65rem;
  &:hover {
    animation-duration: 3s;
  }
  @media (max-width: 562px) {
    display: ${props => (props.toggle ? "none" : "block")};
    width: 110px;
    height: 110px;
    transform: translateX(-50%) translateY(0%);
    animation-name: spin360;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;

    @keyframes spin360 {
      0% {
        transform: translateX(-50%) translateY(0%);
      }
      50% {
        transform: translateX(-50%) translateY(2%) rotateY(180deg);
      }
      100% {
        transform: translateX(-50%) translateY(0%) rotateY(360deg);
      }
    }
  }
  @media (min-width: 762px) {
    display: none;
    width: 125px;
    height: 125px;
    top: ${props => (props.toggle ? "0" : "5%")};
    animation-name: spin360;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    transform: translateX(-45%) translateY(10%);

    @keyframes spin360 {
      0% {
        transform: translateX(-45%) translateY(10%);
      }
      50% {
        transform: translateX(-45%) translateY(8%) rotateY(180deg);
      }
      100% {
        transform: translateX(-45%) translateY(10%) rotateY(360deg);
      }
    }
  }
  @media (min-width: 992px) {
    display: none;
    width: 125px;
    height: 125px;
    top: 0;
    animation-name: spin360;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    transform: translateX(-45%) translateY(10%);

    @keyframes spin360 {
      0% {
        transform: translateX(-45%) translateY(10%);
      }
      50% {
        transform: translateX(-45%) translateY(8%) rotateY(180deg);
      }
      100% {
        transform: translateX(-45%) translateY(10%) rotateY(360deg);
      }
    }
  }
`

const Logo = styled.div`
  font-size: 1.2rem;
  font-weight: bold;
  text-align: center;
  position: relative;
  left: ${props => (props.toggle ? "0" : "0")};
  font-family: "Stylish", sans-serif;
  color: #fff;
  cursor: pointer;
  width: auto;
  z-index: 1;
`

const JPLogo = styled.img`
  @media (max-width: 562px) {
    display: ${props => (props.toggle ? "block" : "none")};
    margin: 0 auto;
    width: 50%;
  }
  width: 60%;
  margin-bottom: 0;
`

const ToggleIcon = styled.img`
  position: absolute;
  top: 50%;
  left: 90%;
  transform: translateX(-100%);
  width: 2rem;
  height: 4.5%;
  fill: #fff;
  display: none;
  &:hover {
    cursor: pointer;
  }
  transition: all 0.5s;

  @media (max-width: 576px) {
    display: block;
  }

  @media (min-width: 768px) {
    display: none;
  }

  @media (min-width: 992px) {
    display: none;
  }
`
const FooterNav = styled.div`
  position: absolute;
  bottom: 3%;
  left: 50%;
  width: 100%;
  transform: translate(-50%);
  display: flex;
  flex-direction: column;
  padding: 0.5rem 1rem;
  text-align: left;
  font-family: "Stylish", sans-serif;
  & span {
    display: flex;
    align-items: center;
    margin: 0.2rem 0;
  }
  & span a {
    text-decoration: none;
    color: #fff;
    margin-left: 0.5rem;
  }

  & span a:hover {
    color: #d1b464;
  }
  font-size: 0.9rem;
  transition: all 0.2 ease;

  @media (max-width: 562px) {
    display: ${props => (props.toggle ? "block" : "none")};
    margin-bottom: 3rem;
  }
  @media (min-width: 762px) {
    margin-bottom: 0rem;
  }
  @media (min-width: 992px) {
    margin-bottom: 0rem;
  }
`

const LinkButton = styled.button`
  background-color: transparent;
  color: #fff;
  border: none;
  margin-left: 0.5rem;
  padding-left: 0;
  text-align: left;
  cursor: pointer;
  & :hover {
    color: #d1b464;
    font-size: 0.9rem;
    transition: all 0.2 ease;
  }

  & :focus {
    outline: none;
  }
`

const SocialIcon = styled.img`
  height: 1rem;
  width: 1rem;
  margin: 0;
`

const Navbar = ({ siteTitle }) => {
  const [
    menu,
    setMenu,
    articleActive,
    setArticle,
    projectActive,
    setProject,
    contactActive,
    setContact,
  ] = useContext(MenuState)

  const [mobileMenu, setMobileMenu] = useState(false)

  return (
    <NavbarStyle bgImage={BlackBG} toggle={mobileMenu}>
      <Logo onClick={() => setMobileMenu(!mobileMenu)} toggle={mobileMenu}>
        <JPLogo src={JpLogo} alt="john phung logo" toggle={mobileMenu} />
        <SwordImage src={Sword} alt="john phung" toggle={mobileMenu} />
      </Logo>
      <Menu toggle={mobileMenu} />

      {mobileMenu && (
        <ToggleIcon src={MenuIcon} onClick={() => setMobileMenu(!mobileMenu)} />
      )}

      <FooterNav toggle={mobileMenu}>
        <span>
          <SocialIcon src={GithubIcon} />
          <a href="https://github.com/jphungcode">Github</a>
        </span>
        <span>
          <SocialIcon src={LinkedIcon} />
          <a href="https://www.linkedin.com/in/john-phung-0791817a/">
            LinkedIn
          </a>
        </span>
        <span>
          <SocialIcon src={EmailIcon} />
          <LinkButton
            onClick={() => {
              setContact(!contactActive)
              setMenu(false)
            }}
          >
            Email
          </LinkButton>
        </span>
      </FooterNav>
    </NavbarStyle>
  )
}

Navbar.propTypes = {
  siteTitle: PropTypes.string,
}

Navbar.defaultProps = {
  siteTitle: ``,
}

export default Navbar
