/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React, { useState, createContext } from "react"
//import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"
import Navbar from "../components/Navbar"
import MainBG from "../images/stock-edit.png"
import Sun from "../images/sun-image.png"
import Contact from "../components/Contact"
import Confirm from "../components/Confirm"
import "./layout.css"

import styled, { ThemeProvider } from "styled-components"

export const theme = {
  primaryColor: "#333",
}

const Container = styled.div`
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-size: 62.5%;
  font-family: "Montserrat", sans-serif;
  display: flex;
  background: url(${MainBG});
  @media (max-width: 568px) {
    background-position: 62%;
  }
  @media (min-width: 768px) {
    background-position: center;
  }
  @media (min-width: 992px) {
    background-position: center;
  }
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: fixed;
  overflow: auto;
`

const MainContent = styled.div`
  width: 100%;
  @media (max-width: 568px) {
    margin-left: 10rem;
  }
  @media (min-width: 768px) {
    margin-left: 10rem;
  }
  @media (min-width: 992px) {
    margin-left: 10rem;
  }
`

const SunImage = styled.img`
  position: fixed;
  z-index: -1;
  @media (max-width: 562px) {
    width: 15rem;
    height: 15rem;

    left: 30%;
    top: 50%;
    animation-name: float;
    animation-duration: 30s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
  @media (min-width: 768px) {
    width: 16rem;
    height: 16rem;

    left: 50%;
    top: 47%;
    animation-name: float;
    animation-duration: 30s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
  @media (min-width: 992px) {
    width: 17rem;
    height: 17rem;

    left: 48%;
    top: 47%;
    animation-name: float;
    animation-duration: 30s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  @keyframes float {
    0% {
      transform: translateY(0) scale(0.9);
    }
    50% {
      transform: translateY(-40px) scale(1);
    }
    100% {
      transform: translateY(0px) scale(0.9);
    }
  }
`

export const MenuState = createContext()

const Layout = ({ children }) => {
  const [menu, setMenu] = useState(false)
  const [articleActive, setArticle] = useState(false)
  const [projectActive, setProject] = useState(false)
  const [contactActive, setContact] = useState(false)
  const [confirm, setConfirm] = useState({ state: "", msg: "" })
  const [blurbActive, setBlurb] = useState(true)

  return (
    <StaticQuery
      query={graphql`
        query SiteTitleQuery {
          site {
            siteMetadata {
              title
            }
          }
        }
      `}
      render={data => (
        <MenuState.Provider
          value={[
            menu,
            setMenu,
            articleActive,
            setArticle,
            projectActive,
            setProject,
            contactActive,
            setContact,
            confirm,
            setConfirm,
            blurbActive,
            setBlurb,
          ]}
        >
          <SunImage src={Sun} />
          <Container>
            <ThemeProvider theme={theme}>
              <Navbar siteTitle={data.site.siteMetadata.title} />
            </ThemeProvider>
            <ThemeProvider theme={theme}>
              <MainContent>{children}</MainContent>
            </ThemeProvider>
            <Contact props={{ contactActive, setContact, setConfirm }} />
          </Container>
          <Confirm props={{ confirm, setConfirm }} />
        </MenuState.Provider>
      )}
    />
  )
}

export default Layout
