import React, { useState, useRef } from "react"
import { useSpring, animated } from "react-spring"
import Recaptcha from "react-google-recaptcha"
import styled from "styled-components"
import BG3 from "../images/container_bg2.png"
import ButtonBG from "../images/yellowstroke.svg"
import Cross from "../images/cross.svg"

const RECAPTCHA_KEY =
  process.env.SITE_RECAPTCHA_KEY || "6Lfd-6IUAAAAAFY7vzYiqNCOAqTwAKdr-b_jXuGc"

const ContactForm = styled(animated.div)`
  ...;
  position: absolute;

  transform: translateX(-50%);

  z-index: 999;
  color: #fff;
  font-size: 1rem;
  display: ${props => (props.open ? "block" : "none")};
  @media (max-width: 562px) {
    background: url(${BG3});
    background-position: center;
    background-size: 90% 90%;
    background-repeat: no-repeat;
    padding: 2rem 2.5rem;
    width: 100%;
    left: 40%;
    height: 80vh;
    top: 10%;
  }
  @media (min-width: 762px) {
    background: url(${BG3});
    background-position: center;
    background-size: 90% 90%;
    background-repeat: no-repeat;
    padding: 3.5rem 3rem;
    width: 45%;
    left: 45%;
    height: 80vh;
    top: 10%;
  }
  @media (min-width: 992px) {
    background: url(${BG3});
    padding: 2rem 3.5rem;
    background-position: top;
    background-size: 90% 100%;
    background-repeat: no-repeat;
    width: 35%;
    left: 45%;
    height: 80vh;
    top: 5%;
  }

  & h1 {
    margin-bottom: 0.5rem;
  }

  & p {
    margin-bottom: 0.5rem;
  }
`

const InputField = styled.input`
  width: 100%;
  margin-top: 0.25rem;
  border: 1px solid #d9a522;
  border-radius: 0.25rem;
  padding-left: 0.25rem;
`
const TextField = styled.textarea`
  width: 100%;
  margin-top: 0.25rem;
  border: 1px solid #d9a522;
  border-radius: 0.25rem;
  padding-left: 0.25rem;
`

const SubmitButton = styled.input`
  border: none;
  float: right;
  padding: 0.5rem 0.5rem 0.5rem 3rem;

  font-family: "Stylish", sans-serif;
  font-size: 1.3rem;
  transition: 0.2s all ease;
  cursor: pointer;
  @media (max-width: 562px) {
    background: url(${ButtonBG});
    background-position: center;
    background-size: 100%;
    background-repeat: no-repeat;
    padding: 0.5rem 0.5rem 0.5rem 37%;
    width: 70%;
  }
  @media (min-width: 762px) {
    background: url(${ButtonBG});
    background-position: center;
    background-size: 100%;
    background-repeat: no-repeat;
    padding: 0.5rem 0.5rem 0.5rem 50%;
    width: 100%;
  }
  @media (min-width: 992px) {
    background: url(${ButtonBG});
    background-position: right;
    background-size: 100%;
    background-repeat: no-repeat;
    padding: 0.5rem 0.5rem 0.5rem 3rem;
    width: 55%;
  }

  &:hover {
    filter: invert(1);
  }

  &:focus {
    outline: none;
  }
`

const FormLabel = styled.label``

const CloseButton = styled.button`
  position: absolute;
  right: 10%;
  top: 10%;
  width: 30px;
  height: 30px;
  border: none;
  background: url(${Cross});
  background-position: center;
  background-size: 90% 90%;
  background-repeat: no-repeat;
  cursor: pointer;
  &:hover {
    filter: invert(0.5);
  }
`

function encode(data) {
  return Object.keys(data)
    .map(key => encodeURIComponent(key) + "=" + encodeURIComponent(data[key]))
    .join("&")
}

const ModalContainer = styled.div`
  @media (max-width: 562px) {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    display: ${props => (props.open ? "block" : "none")};
  }
  @media (min-width: 762px) {
    width: 100vw;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    display: ${props => (props.open ? "block" : "none")};
  }
  @media (min-width: 992px) {
    width: 100vw;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    display: ${props => (props.open ? "block" : "none")};
  }
`

const Contact = ({ props: { contactActive, setContact, setConfirm } }) => {
  const formFade = useSpring({
    opacity: contactActive ? 1 : 0,
    transform: contactActive ? "translateX(-40%)" : "translateX(-50%)",
    duration: 500,
  })

  const captchaRef = useRef("recaptcha")

  const [name, setName] = useState("")
  const [email, setEmail] = useState("")
  const [message, setMessage] = useState("")
  const [captcha, setCaptcha] = useState("")

  const handleSubmit = e => {
    e.preventDefault()
    const form = e.target
    fetch("/", {
      method: "POST",
      headers: { "Content-Type": "application/x-www-form-urlencoded" },
      body: encode({
        "form-name": form.getAttribute("name"),
        name: name,
        email: email,
        message: message,
        "g-recaptcha-response": captcha,
      }),
    })
      .then(() => {
        setConfirm({ state: "success", msg: "Message successfully sent!" })
        setName("")
        setEmail("")
        setMessage("")
        setContact(false)
      })
      .catch(error => {
        setConfirm({ state: "error", msg: "There was an error." })
      })
  }

  return (
    <ModalContainer open={contactActive}>
      <ContactForm style={formFade} open={contactActive}>
        <h1>Hello.</h1>
        <p>Submit a form to get to touch</p>
        <form
          name="contact-recaptcha"
          method="post"
          action="/thanks/"
          data-netlify="true"
          data-netlify-recaptcha="true"
          onSubmit={handleSubmit}
        >
          <noscript>
            <p>This form won’t work with Javascript disabled</p>
          </noscript>
          <FormLabel>
            Your name:
            <br />
            <InputField
              type="text"
              name="name"
              value={name}
              onChange={e => setName(e.target.value)}
            />
          </FormLabel>
          <FormLabel>
            Your email:
            <br />
            <InputField
              type="email"
              name="email"
              value={email}
              onChange={e => setEmail(e.target.value)}
            />
          </FormLabel>
          <FormLabel>
            Message:
            <br />
            <TextField
              name="message"
              rows="4"
              value={message}
              onChange={e => setMessage(e.target.value)}
            />
          </FormLabel>
          <Recaptcha
            ref={captchaRef}
            sitekey={RECAPTCHA_KEY}
            onChange={value => setCaptcha(value)}
          />
          <SubmitButton type="submit" value="SEND" />
        </form>
        <CloseButton onClick={() => setContact(false)} />
      </ContactForm>
    </ModalContainer>
  )
}

export default Contact
